.shop-header {
  border-bottom: 2px solid rgba(95, 126, 95, 0.808);
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  background: #203127a8;
}

.shop-header a {
  color: rgb(158, 199, 142);
  text-decoration: none;
}
.shop-header a:hover {
  color: rgba(75, 255, 4, 0.411);
}

.shop-header .logo {
  font-family: 'Hanalei', cursive;
  font-size: 4rem;
  padding-left: 1rem;
}

.shop-header .shopping-cart {
  align-self: center;
  font-size: 1.3rem;
  padding-right: 1rem;
  text-align: center;
}

.shop-header .shopping-cart .cart-icon {
  font-size: 2.2rem;
  margin-right: 10px;
}


.preview-item {
  display: flex;
  align-items: center;
  margin: 1vh;
  padding: 1vh;
  height: 195px;
  width: 350px;
  border: .5px solid #2e4232;
  border-radius: 5px;
  background: #071508a1;
  box-shadow: 5px 5px 30px 7px rgba(0, 0, 0, 0.25), -5px -5px 30px 7px rgba(0, 0, 0, 0.22); }
  .preview-item img {
    width: 150px;
    height: 143px;
    margin: 1vh; }
  .preview-item span {
    margin: 0 10px; }
  .preview-item .item-details {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 10px;
    align-items: flex-start; }
    .preview-item .item-details .item-title {
      font-size: 1.2rem;
      font-weight: bold;
      color: #fff;
      margin: 0 10px; }
    .preview-item .item-details .item-price {
      font-size: 1.4rem; }
    .preview-item .item-details button {
      width: 120px;
      margin: 5px auto; }



.item-list {
  list-style: none;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.shopping-cart-table {
  margin-top: 45px; }
  .shopping-cart-table .shopping-cart-board {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap; }
  .shopping-cart-table .total {
    text-align: right;
    font-size: 1.3rem;
    margin-right: 10px; }
  .shopping-cart-table button {
    margin-left: 5px;
    margin-top: 5px; }

.shopping-card {
  position: relative;
  width: 200px;
  height: 220px;
  border: 1px solid rgba(255, 255, 255, 0.377);
  background: rgba(0, 0, 0, 0.61);
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 3vh;
  padding: 15px; }
  .shopping-card img {
    width: 120px;
    height: 120px; }
  .shopping-card .delete {
    position: absolute;
    top: 0;
    right: 4%;
    color: red;
    font-size: 20px;
    cursor: pointer;
    -webkit-transition: .3s;
    transition: .3s; }
  .shopping-card .delete:active {
    text-shadow: 1px 1px 2px black, 0 0 1em red; }
  .shopping-card .total-info {
    display: flex;
    align-items: center; }
    .shopping-card .total-info button:nth-child(1), .shopping-card .total-info button:nth-child(3) {
      color: rgba(255, 255, 255, 0.452);
      border: 1px solid rgba(255, 255, 255, 0.452);
      border-radius: 50%;
      width: 30px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 10px;
      -webkit-transition: .3s;
      transition: .3s; }
    .shopping-card .total-info button:nth-child(1):hover, .shopping-card .total-info button:nth-child(3):hover {
      border: 1px solid rgba(255, 255, 255, 0.733); }
    .shopping-card .total-info button:nth-child(1):active {
      color: rgba(192, 24, 24, 0.959); }
    .shopping-card .total-info button:nth-child(3):active {
      color: rgba(38, 230, 47, 0.959); }

.preview-item-details {
  display: flex;
  align-items: flex-start;
}
.item-details-img {
  width: 200px;
  height: auto;
}

.item-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.item-details button {
  width: 12px;
  margin: 5vh 0;
}

.item-card {
  display: flex;
  flex-direction: column;
  width: 200px;
  margin: 0 5vh;
}
body {
  background: url(/IlyaOrluk/Game-store/static/media/bg2.e2d95412.jpg) no-repeat fixed center/cover;
  color: #fff;
  margin: 0;
  padding: 0; }

hr {
  margin-top: 1rem;
  margin-bottom: 1rem;
  border: 0;
  border-top: 1px solid rgba(255, 255, 255, 0.48);
  width: 100%; }

