.shopping-cart-table {
  margin-top: 45px;
  .shopping-cart-board {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
  }
  .total {
    text-align: right;
    font-size: 1.3rem;
    margin-right: 10px;
  }
  button {
    margin-left: 5px;
    margin-top: 5px;
  }
}

.shopping-card {
  position: relative;
  width: 200px;
  height: 220px;
  border: 1px solid rgba(255, 255, 255, 0.377);
  background: rgba(0, 0, 0, 0.61);
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 3vh;
  padding: 15px;


  img {
    width: 120px;
    height: 120px;
  }
  .delete {
    position: absolute;
    top: 0;
    right: 4%;
    color: rgb(255, 0, 0);
    font-size: 20px;
    cursor: pointer;
    transition: .3s;
  }
  .delete:active {
    text-shadow: 1px 1px 2px black, 0 0 1em red;
  }
  .total-info {
    display: flex;
    align-items: center;
    button:nth-child(1), button:nth-child(3) {
      color:  rgba(255, 255, 255, 0.452);
      border: 1px solid rgba(255, 255, 255, 0.452);
      border-radius: 50%;
      width: 30px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 10px;
      transition: .3s;
    }
    button:nth-child(1):hover, button:nth-child(3):hover {
      border: 1px solid rgba(255, 255, 255, 0.733);
    }
    button:nth-child(1):active {
      color:  rgba(192, 24, 24, 0.959);
    }
    button:nth-child(3):active {
      color:  rgba(38, 230, 47, 0.959);
    }
  }

}
